@import "src/utils/mixins";
@import "src/utils/variables";

.experience {
  position: relative;
  transition: .3s ease transform;
  transform: scale(1) translate(0, 0);
  z-index: 2;
  border: 1px solid #333;

  .infoWrapper {
    background: #222;
    min-height: $listHeight;
    .infoField {
      display: flex;
      flex-wrap: wrap;
      padding: 24px;
      font-family: 'Lato', sans-serif;
      .skillWrapper {
        width: 50%;
        h6 {
          margin-bottom: 0;
          color: #fff;
          font-size: 15px;
          margin-top: 0;
          text-transform: uppercase;
          font-weight: 400;
          text-align: center;
        }
      }
    }
  }
}
@include hidePositions;